import React from 'react'
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import SearchScreen from 'views/mobileView/components/pages/SearchScreen';

export default function FundSearch(props) {

  let type = props.match.params.type;
  type = type === 'ETFs' ? 1 : type === 'MutualFunds' ? 2 : 3;

  const onSearchResultClick = (item) => {
    console.log(item)
    if(type===1){
      props.history.push("/ETF-detail/"+item.Symbol)
    } else if(type===2){
      props.history.push("/fund-detail/"+item.FundCode)
    } else {
      props.history.push("/mintbox-detail/"+item.MintboxId)
    }
  }


  return (
    <div className='mainContainer'>
      <MainHeader
        header={type === 1 ? 'Invest in low cost ETFs' : type === 2 ? 'Direct mutual funds' : 'Invest in mintboxes'}
        right={type === 3? '':'mintboxes'} rightAction={()=>{props.history.push('/Invest')}}
        navigation={props.history}
      />
      <div className="dataContainer px-0">
        <div>
          <SearchScreen 
          searchText ={type === 1 ? 'Search by ETF name' : type === 2 ? 'Search by mutual fund name' : 'Search by mintbox name'}
          navigation={props.history}
          type={type}
          onCardClick={(item) => onSearchResultClick(item)}
          /> 
        </div>
      </div>
    </div>
  )
}
